import { useState, useEffect} from 'react';
import { useMoralisWeb3Api, useMoralis } from "react-moralis";
import {ABI,ADDRESS} from "../../config/constant";
import Web3 from "web3";
import './Account.css';

const Account = ({userAddress}) => {
  const Web3Api = useMoralisWeb3Api();
  const { isInitialized } = useMoralis();
  const [fusionUsdPrice, setFusionUsdPrice] = useState(0);
  const [userFusionBalance, setUserFusionBalance] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [taxFreeGain, setTaxFreeGain] = useState(0);
  const [busdRewards, setBusdRewards] = useState(0);
  const [minutes, setMinutes ] = useState(15);
  const [seconds, setSeconds ] =  useState(0);

  const initializeContract = async(abi, address) => {
    let web3 = new Web3( window.ethereum);
    let res = new web3.eth.Contract(abi, address);
    return res;
  }

  const getFusionPrice = async() => {
    let price = await Web3Api.token.getTokenPrice({chain: "bsc", address: ADDRESS});
    setFusionUsdPrice(price.usdPrice.toFixed(7));
  }

  const getUserBalance = async() => {
    setIsLoading(true);
    let tokenBalances = await Web3Api.account.getTokenBalances({chain: "bsc", address: userAddress});

    let itemsProcessed = 0;
    if(tokenBalances.length == 0){
      setIsLoading(false);
    }
    tokenBalances.forEach((token, index, array) => {
      // if(token.symbol == "CA" && token.name == "Contract"){
      //   setUserFusionBalance(token.balance/(Math.pow(10,token.decimals)));
      //   setIsLoading(false);
      // }

      if(token.token_address == ADDRESS.toLowerCase()){
        setUserFusionBalance(token.balance/(Math.pow(10,token.decimals)));
        setIsLoading(false);
      }
      itemsProcessed++;
      if(itemsProcessed == array.length){
        setIsLoading(false);
      }
    })
  }

  const getTaxFreeGain = async() => {
    let contract_instance = await initializeContract(ABI,ADDRESS);
    let taxFreeGain = await contract_instance.methods.getReflectionlessBalance(userAddress).call();
    let balance = await contract_instance.methods.balanceOf(userAddress).call();
    setTaxFreeGain(((balance/Math.pow(10,5)) - (taxFreeGain/Math.pow(10,5))).toFixed(7));
  }

  const getBusdRewards = async() => {
    let contract_instance = await initializeContract(ABI,ADDRESS);
    let busdRewards = await contract_instance.methods.getUserRealizedGains(userAddress).call();
    setBusdRewards(busdRewards/Math.pow(10,18));
  }

  useEffect(() => {
    let myInterval = setInterval(() => {
            if (seconds > 0) {
                setSeconds(seconds - 1);
            }
            if (seconds === 0) {
                if (minutes === 0) {
                    setMinutes(15)
                    // clearInterval(myInterval)
                } else {
                    setMinutes(minutes - 1);
                    setSeconds(59);
                }
            } 
        }, 1000)
        return () => {
            clearInterval(myInterval);
        };
  });

  useEffect(() => {
    if(isInitialized){
      getUserBalance();
      getFusionPrice();
    }

    if(userAddress){
      getBusdRewards();
      getTaxFreeGain();
    }
  }, [isInitialized, userAddress])

  return (
    <>
      <div className="Account">
        <div className="container px-0 px-lg-3">
          <div className="row justify-content-center mx-0">
            <div className="col-lg-10 col-12 px-0 px-lg-3">
              <div className="account-card mb-5">
                <div className="glow-green p-3 d-inline-block">
                <div className="fs-16 textGray fw-500">Total Earned</div>
                <div className="textGreen fs-40 fw-600">${(userFusionBalance * fusionUsdPrice).toFixed(4)}</div>
                <div className="fs-16 textGray fw-500">{userFusionBalance.toFixed(2).toLocaleString()} FUUSION ({userFusionBalance==0?'0.00':'100.00'}% increase)</div>
                </div>
              </div>
              <div className="account-card pl-0 pl-lg-3 pt-lg-0 pb-0 pr-0 mb-5">
                <div className="row mx-0 justify-content-between">
                  <div className="col-lg-6 px-0 mb-4 mb-lg-0">
                    <div className="d-flex flex-column ml-3 ml-lg-0 justify-content-center h-100">
                      <div className="glow-green p-3">
                      <div className="fs-16 fw-400 textGray">APY</div>
                      <div className="fs-22 fw-600 text-white">100,000%</div>
                      <div className="fs-16 fw-400 textGray">Daily ROI 2.00%</div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-6 px-0">
                    <div className="account-card">
                      <div className="glow-pink p-3">
                      <div className="fs-16 fw-400 textGray">Your Balance</div>
                      {isLoading && <div className="loader loaderText my-2"></div>}
                      {!isLoading && <div className="fs-22 fw-600 text-white">{userFusionBalance.toFixed(2).toLocaleString()}</div>}
                      <div className="fs-16 fw-400 textGray">FUUSION</div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-6 px-0 mb-4 mb-lg-0">
                    <div className="d-flex flex-column ml-3 ml-lg-0 justify-content-center h-100">
                      <div className="glow-green p-3">
                      <div className="fs-16 fw-400 textGray">Tax Free Gains</div>
                      <div className="fs-22 fw-600 text-white">$ {taxFreeGain * fusionUsdPrice}</div>
                      <div className="fs-16 fw-400 textGray">{taxFreeGain} FUUSION</div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-6 px-0">
                    <div className="account-card">
                      <div className="glow-pink p-3">
                      <div className="fs-16 fw-400 textGray">BUSD Rewards</div>
                      {/* <div className="loader loaderText my-2"></div> */}
                      <div className="fs-22 fw-600 text-white">{busdRewards}</div>
                      <div className="fs-16 fw-400 textGray">BUSD</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="account-card pl-0 pl-lg-3 pt-lg-0 pb-0 pr-0 mb-5">
                <div className="row mx-0 justify-content-between">
                  <div className="col-lg-6 px-0 mb-4 mb-lg-0">
                    <div className="d-flex flex-column ml-3 ml-lg-0 justify-content-center h-100">
                      <div className="glow-green p-3">
                      <div className="fs-16 fw-400 textGray">Next Rebase:</div>
                      <div className="fs-22 fw-600 text-white">00:{minutes}:{seconds < 10 ?  `0${seconds}` : seconds}</div>
                      <div className="fs-16 fw-400 textGray">Interest Coming In Your Wallet</div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-6 px-0">
                    <div className="account-card">
                      <div className="glow-pink p-3">
                      <div className="fs-16 fw-400 textGray">Your Earnings/Daily</div>
                      {isLoading && <div className="loader loaderText my-2"></div>}
                      {!isLoading && <div className="fs-22 fw-600 text-white">${(userFusionBalance * fusionUsdPrice * 0.0228).toFixed(4)}</div>}
                      {!isLoading && <div className="fs-16 fw-400 textGray">{(userFusionBalance * 0.0228).toFixed(2)} FUUSION</div>}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row justify-content-center mx-0">
            <div className="col-lg-10 col-12 px-0 px-lg-3">
              <div className="account-card">
                <div className="d-flex align-items-center justify-content-between my-4">
                  <div className="fs-16 fw-500 textGray">Current FUUSION Price</div>
                  <div className="fs-16 fw-500 textGray">${fusionUsdPrice}</div>
                </div>
                {/* <div className="d-flex align-items-center justify-content-between">
                  <div className="fs-16 fw-500 textGray">Next Reward Amount USD</div>
                  <div className="fs-16 fw-500 textGreen">USD</div>
                </div> */}
                <div className="d-flex align-items-center justify-content-between my-4">
                  <div className="fs-16 fw-500 textGray">Next Reward Yield</div>
                  <div className="fs-16 fw-500 textGray">0.02084%</div>
                </div>
                <div className="d-flex align-items-center justify-content-between">
                  <div className="fs-16 fw-500 textGray">ROI(1-Day Rate) USD</div>
                  <div className="fs-16 fw-500 textGreen">${(userFusionBalance * fusionUsdPrice * 0.02084).toFixed(4)}</div>
                </div>
                <div className="d-flex align-items-center justify-content-between my-4">
                  <div className="fs-16 fw-500 textGray">ROI(5-Day Rate)</div>
                  <div className="fs-16 fw-500 textGreen">10.00%</div>
                </div>
                <div className="d-flex align-items-center justify-content-between">
                  <div className="fs-16 fw-500 textGray">ROI(5-Day Rate) USD</div>
                  <div className="fs-16 fw-500 textGreen">${(userFusionBalance * fusionUsdPrice * 0.10).toFixed(4)}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}

export default Account