import React from "react";
import './Swap.css';
import { BigNumber } from "bignumber.js";
import { useState, useEffect} from 'react';
import Icon from '../../assets/icon.png';
import Web3 from "web3";
import {TOKEN_ABI, SWAP_CONTRACT_ADDRESS, SWAP_ABI, ADDRESS} from "../../config/constant";
import { useMoralisWeb3Api, useMoralis } from "react-moralis";
import swapIcon from '../../assets/Swap_Button.svg';
/* global BigInt */

const Swap = ({userAddress}) => {
    const Web3Api = useMoralisWeb3Api();
    const { isInitialized } = useMoralis();
    const [selectedFrom, setSelectedFrom] = useState({
        symbol: 'BNB',
        name: 'Binance Coin',
        address: '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c',
        decimals: 18,
    });

    // const [selectedTo, setSelectedTo] = useState({
    //     symbol: 'CA',
    //     name: 'Contract',
    //     address: ADDRESS,
    //     decimals: 5,
    // });

    const [selectedTo, setSelectedTo] = useState({
        symbol: 'FUU',
        name: 'Fuusion',
        address: ADDRESS,
        decimals: 5,
    });

    const [selectedFromBalance, setSelectedFromBalance] = useState(0);
    const [selectedToBalance, setSelectedToBalance] = useState(0);
    const [tokenBalances, setTokenBalances] = useState()
    const [fromInput, setFromInput] = useState(0);
    const [toInput, setToInput] = useState(0);
    const [isExceeded, setIsExceeded] = useState(false);
    const [isEnabled, setIsEnabled] = useState(false);

    const fromOptions = [
        {
            symbol: 'BNB',
            name: 'Binance Coin',
            address: '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c',
            decimals: 18,
        },
        // {
        //     symbol: 'CA',
        //     name: 'Contract',
        //     address: '0xD52b98694fAC9ddbFBAc56f5211D3F305678A32a',
        //     decimals: 5
        // },
        {
            symbol: 'FUU',
            name: 'Fuusion',
            address: '0x8ABaD40f5F0cdabde1D79da75DB8a87c825e2ecA',
            decimals: 5
        },
        {
            symbol: 'BUSD',
            name: 'BUSD Token',
            address: '0xe9e7cea3dedca5984780bafc599bd69add087d56',
            decimals: 18,
        },
        // {
        //     symbol: 'PFY',
        //     name: 'Portify',
        //     address: '0x69083b64988933E8B4783E8302b9bBf90163280E',
        //     decimals: 9,
        // }
    ]

    const toOptions = [
        {
            symbol: 'BNB',
            name: 'Binance Coin',
            address: '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c',
            decimals: 18,
        },
        // {
        //     symbol: 'CA',
        //     name: 'Contract',
        //     address: '0xD52b98694fAC9ddbFBAc56f5211D3F305678A32a',
        //     decimals: 5,
        // },
        {
            symbol: 'FUU',
            name: 'Fuusion',
            address: '0x8ABaD40f5F0cdabde1D79da75DB8a87c825e2ecA',
            decimals: 5
        },
        {
            symbol: 'BUSD',
            name: 'BUSD Token',
            address: '0xe9e7cea3dedca5984780bafc599bd69add087d56',
            decimals: 18,
        },
        {
            symbol: 'PFY',
            name: 'Portify',
            address: '0x69083b64988933E8B4783E8302b9bBf90163280E',
            decimals: 9,
        }
    ]

    const initializeContract = async(abi, address) => {
        let web3 = new Web3( window.ethereum);
        let res = new web3.eth.Contract(abi, address);
        return res;
    }

    const checkAllowance = async(tokenAddress) => {
        const contract_instance = await initializeContract(TOKEN_ABI, tokenAddress);
        let allowance = await contract_instance.methods.allowance(userAddress,SWAP_CONTRACT_ADDRESS).call();
        if(allowance == 0){
            setIsEnabled(false);
        }else{
            setIsEnabled(true);
        }
    }

    const enable = async(tokenAddress) => {
        const contract_instance = await initializeContract(TOKEN_ABI, tokenAddress);
        await contract_instance.methods.approve(
            SWAP_CONTRACT_ADDRESS,
            BigInt(10 ** 28).toString()
        ).send({
            from: userAddress
        });
        setIsEnabled(true);
    }

    const getInitialBalances = async() => {
        let balances = await Web3Api.account.getTokenBalances({chain: 'bsc', address: userAddress });
        setTokenBalances(balances);
        let nativeBalance = await Web3Api.account.getNativeBalance({chain: 'bsc', address: userAddress });
        setSelectedFromBalance(nativeBalance.balance/Math.pow(10,18));
        // let fuusion = balances.filter((token) => token.symbol == 'CA' && token.name == 'Contract');
        let fuusion = balances.filter((token) => token.token_address == ADDRESS.toLowerCase());
        setSelectedToBalance(fuusion[0].balance/Math.pow(10,5) || 0)
    }

    const onFromSelectClick = async(option) => {
        console.log(tokenBalances);
        if(option.symbol == selectedTo.symbol){
            onSwapClick();
        }else{
            setSelectedFrom(option);
            if(option.symbol == 'BNB'){
                let nativeBalance = await Web3Api.account.getNativeBalance({chain: 'bsc', address: userAddress });
                setSelectedFromBalance(nativeBalance.balance/Math.pow(10,18));
            }else{
                let selectedToken = tokenBalances.filter(x => x.symbol == option.symbol && x.name == option.name);
                if(selectedToken.length == 0){
                    setSelectedFromBalance(0);
                }else{
                    setSelectedFromBalance(selectedToken[0].balance/Math.pow(10, selectedToken[0].decimals));
                }
            }
        }  
    }

    const onToSelectClick = async(option) => {
        if(option.symbol == selectedFrom.symbol){
            onSwapClick();
        }else{
            setSelectedTo(option);
            setToInput(0);
            if(option.symbol == 'BNB'){
                let nativeBalance = await Web3Api.account.getNativeBalance({chain: 'bsc', address: userAddress });
                setSelectedToBalance(nativeBalance.balance/Math.pow(10,18));
            }else{
                let selectedToken = tokenBalances.filter(x => x.symbol == option.symbol && x.name == option.name);
                if(selectedToken.length == 0){
                    setSelectedToBalance(0);
                }else{
                    setSelectedToBalance(selectedToken[0].balance/Math.pow(10, selectedToken[0].decimals));
                }
            }
        }
    }

    const onSwapClick = async () => {
        let temp = selectedFrom;
        setSelectedFrom(selectedTo);
        setSelectedTo(temp);

        let temp1 = selectedFromBalance;
        setSelectedFromBalance(selectedToBalance);
        setSelectedToBalance(temp1);

        let temp2 = fromInput;
        setFromInput(toInput);
        setToInput(temp2);
    }

    let fromArray = [], toArray = [];
    for(let i = 0;i < fromOptions.length;i++) {
        fromArray.push(
            <div class="dropdown-item" onClick={() => onFromSelectClick(fromOptions[i])}>
                <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center">
                        {/* <div className="icon"><img src={Icon} alt="" /></div> */}
                        <div className="fs-16 fw-400 textGray mx-3">{fromOptions[i].symbol}</div>
                    </div>
                    <div className="fs-16 fw-400 textGray">{fromOptions[i].name}</div>
                </div>
            </div>
        );
    }

    for(let i = 0;i < toOptions.length;i++) {
        toArray.push(
            <div class="dropdown-item" onClick={() => onToSelectClick(toOptions[i])}>
                <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center">
                        {/* <div className="icon"><img src={Icon} alt="" /></div> */}
                        <div className="fs-16 fw-400 textGray mx-3">{toOptions[i].symbol}</div>
                    </div>
                    <div className="fs-16 fw-400 textGray">{toOptions[i].name}</div>
                </div>
            </div>
        );
    }

    const onFromAmountChange = async(event) => {
        if(event.target.value > selectedFromBalance){
            setIsExceeded(true);
        }else {
            setIsExceeded(false);
        }
        setFromInput(event.target.value);
        if(event.target.value){
            let x1 =  await Web3Api.token.getTokenPrice({chain: 'bsc', address: selectedFrom.address });
            let x2 =  await Web3Api.token.getTokenPrice({chain: 'bsc', address: selectedTo.address });
            let fromUsdAmount = x1.usdPrice * (event.target.value* 0.99);
            setToInput(fromUsdAmount/x2.usdPrice);
        }
    }

    const onToAmountChange = (event) => {
        setToInput(event.target.value);
    }

    const onFromMaxClick = async() => {
        setFromInput(selectedFromBalance * 0.99);
        let x1 =  await Web3Api.token.getTokenPrice({chain: 'bsc', address: selectedFrom.address });
        let x2 =  await Web3Api.token.getTokenPrice({chain: 'bsc', address: selectedTo.address });
        let fromUsdAmount = x1.usdPrice * (selectedFromBalance * 0.99);
        setToInput(fromUsdAmount/x2.usdPrice);
    }

    const swap = async() => {
        const contract_instance = await initializeContract(SWAP_ABI,SWAP_CONTRACT_ADDRESS)
        if(selectedFrom.symbol == 'BNB'){
            let response = await contract_instance.methods.swapExactETHForTokens(
                BigInt(210),
                [selectedFrom.address, selectedTo.address],
                userAddress,
                Date.now() + 86400
            ).send({
                from: userAddress,
                value: fromInput * 10 ** 18
            });
        }else if(selectedTo.symbol == 'BNB'){
            let response = await contract_instance.methods.swapExactTokensForETH(
                BigInt(fromInput * 10 ** Number(selectedFrom.decimals)),
                BigInt(210),
                [selectedFrom.address, selectedTo.address],
                userAddress,
                Date.now() + 86400
            ).send({
                from: userAddress,
            });
        }else{
            console.log(selectedFrom.address, selectedTo.address);
            console.log(fromInput * 10 ** Number(selectedFrom.decimals));
            let response = await contract_instance.methods.swapExactTokensForTokens(
                BigInt(fromInput * 10 ** Number(selectedFrom.decimals)),
                BigInt(210),
                [selectedFrom.address, selectedTo.address],
                userAddress,
                Date.now() + 86400
            ).send({
                from: userAddress,
            });
        }
    }

    useEffect(() => {
        if(isInitialized && userAddress){
            getInitialBalances();
            checkAllowance(selectedFrom.address);
        }else{
            setSelectedFromBalance(0);
            setSelectedToBalance(0);
            setFromInput(0);
            setToInput(0);
        }
    }, [isInitialized, userAddress]);

    useEffect(() => {
        if(userAddress){
            checkAllowance(selectedFrom.address);
        }
    },[selectedFrom])

    return (
        <>
            <div className="Swap">
                <div className="container">
                    <div className="row justify-content-center mx-0">
                        <div className="col-lg-5">
                            <div className="swapCard">
                                <div className="fs-24 fw-500 mb-3">Swap</div>
                                <div className="d-flex align-items-center justify-content-between mb-2">
                                    <div className="fs-16 fw-400">From</div>
                                </div>
                                <div className="inputDrop">
                                    <input type="number" className="firstInput" placeholder="0.0" value={fromInput} onChange={onFromAmountChange}/>
                                    <div class="dropdown">
                                        <a class="dropdown-toggle textGray" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-expanded="false">
                                            <div className="d-inline-flex flex-column">
                                                <div className="fs-16 fw-700 text-white">{selectedFrom.symbol}</div>
                                                <div className="fs-12 fw-400 textGray">{selectedFrom.name}</div>
                                            </div>
                                        </a>
                                        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                            <div className="dropDown-search mb-3">
                                                <input type="text" />
                                                <button type="button" className="pointer btn"><i class="fa-solid fa-magnifying-glass"></i></button>
                                            </div>
                                            <div className="scroll">
                                                {fromArray}
                                            </div>

                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex align-items-center justify-content-between mt-1">
                                    <div className="fs-14 fw-400 text-white">Balance: {selectedFromBalance}</div>
                                    <div className="fs-14 fw-400 textGray cp" onClick={() => onFromMaxClick()}><u>Max</u></div>
                                </div>
                                <div className="d-flex justify-content-center my-3" onClick={() => onSwapClick()}>
                                    <a className="swapRound"><img src={swapIcon} alt="" /></a>
                                </div>
                                <div className="d-flex align-items-center justify-content-between mb-2">
                                    <div className="fs-16 fw-400">To</div>
                                </div>
                                <div className="inputDrop">
                                    <input type="number" className="firstInput" placeholder="0.0" value={toInput} onChange={onToAmountChange}/>
                                    <div class="dropdown">
                                        <a class="dropdown-toggle textGray" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-expanded="false">
                                            <div className="d-inline-flex flex-column">
                                                <div className="fs-16 fw-700 text-white">{selectedTo.symbol}</div>
                                                <div className="fs-12 fw-400 textGray">{selectedTo.name}</div>
                                            </div>
                                        </a>
                                        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                            <div className="dropDown-search mb-3">
                                                <input type="text" />
                                                <button type="button" className="pointer btn"><i class="fa-solid fa-magnifying-glass"></i></button>
                                            </div>
                                            <div className="scroll">
                                                {toArray}
                                            </div>

                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex align-items-center justify-content-between mt-1">
                                    <div className="fs-14 fw-400 text-white">Balance: {selectedToBalance}</div>
                                </div>
                                {userAddress && !isExceeded && isEnabled && <div className="mt-3"><a className="btn btnSwap" onClick={() => swap()}>Swap</a></div>}
                                {userAddress && !isExceeded && !isEnabled && <div className="mt-3" onClick={() => enable(selectedFrom.address)}><a className="btn btnSwap">Enable {selectedFrom.symbol}</a></div>}
                                {userAddress && isExceeded && <div className="mt-3"><a className="btn btnSwap">Insufficient Balance</a></div>}
                                {!userAddress && <div className="mt-3"><a className="btn btnSwap">Connect Wallet</a></div>}
                                {/* <div className="footerText fs-14 fw-400 textGray">Rate: 1 USDT = 0.0020 BNB (~$1.01) <i class="fa-solid fa-circle-info pointer"></i></div> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Swap;