import React from 'react';
import './Sidebar.css';
import { NavLink } from 'react-router-dom';

const Sidebar = ({collapsed, setCollapsed, userAddress}) => {
  const handleClick = () => {
    if(collapsed) {
      setCollapsed(!collapsed);
    }
  }
  return (
    <>

      <div className={collapsed ? 'sideBar sb-mobile' : 'd-none'}>

        <i className="toggle d-block d-lg-none" onClick={() => handleClick()}>x</i>

        <ul className="side-bar">
          {userAddress && <li className="text-white">{userAddress.slice(0,6)+'...'+userAddress.slice(userAddress.length - 4, userAddress.length)}</li>}
          <li>
            <NavLink to="/" activeClassName='active' className="d-inline-flex align-items-center pointer">
              <i className="mr-3 fas fa-home fs-20"></i>
              <div className="fs-20 fw-500">Dashboard</div>
            </NavLink>
          </li>
          <li>
            <NavLink to="/account" activeClassName='active' className="d-inline-flex align-items-center pointer">
              <i className="mr-3 fas fa-user-circle fs-20"></i>
              <div className="fs-20 fw-500">Account</div>
            </NavLink>
          </li>
          <li>
            <NavLink to="/calculator" activeClassName='active' className="d-inline-flex align-items-center pointer">
              <i className="mr-3 fas fa-calculator fs-20"></i>
              <div className="fs-20 fw-500">Calculator</div>
            </NavLink>
          </li>
          <li>
            <NavLink to="/swap" activeClassName='active' className="d-inline-flex align-items-center pointer">
              <i className="mr-3 fas fa-bolt fs-20"></i>
              <div className="fs-20 fw-500">Swap</div>
            </NavLink>
          </li>
          <li>
            <a className="d-flex align-items-center pointer" target="_blank" rel="noopener noreferrer" href='https://uploads-ssl.webflow.com/625e90c17a4d473a4c3e3dc8/627132a0a07d652ac4c4385a_WHITEPAPER.pdf'>
              <i className="mr-3 fas fa-book fs-20"></i>
              <div className="fs-20 fw-500">Docs</div>
            </a>
          </li>
        </ul>
        <ul className="social-icon">
          <li><a target="_blank" rel="noopener noreferrer" href="https://t.me/fuusionfinance"><i className="fab fa-telegram"></i></a></li>
          <li><a target="_blank" rel="noopener noreferrer" href="https://www.youtube.com/channel/UCmvh_EXooQ2SPge6y2V02tw/about"><i class="fab fa-youtube"></i></a></li>
          <li><a target="_blank" rel="noopener noreferrer" href="https://twitter.com/Fuusion_finance"><i className="fab fa-twitter"></i></a></li>
          <li><a target="_blank" rel="noopener noreferrer" href="https://discord.gg/gjY77ByD"><i className="fab fa-discord"></i></a></li>
        </ul>
      </div>
    </>
  )
}

export default Sidebar