import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Header from './components/Layout/Header/Header';
import Sidebar from './components/Layout/Sidebar/Sidebar';
import Dashboard from './components/Dashboard/Dashboard';
import Account from './components/Account/Account';
import Calculator from './components/Calculator/Calculator';
import { useState, useEffect } from 'react';
import Swap from './components/Swap/Swap';

function App() {
  const [collapsed, setCollapsed] = useState(true);
  const [userAddress, setUserWalletAddress] = useState('');

  return (
    <div className='container-fluid'>
      <Router>
        <Header collapsed={collapsed} setCollapsed={setCollapsed} setUserWalletAddress={setUserWalletAddress}/>
        <div className="row mx-0">
          <div>
            <Sidebar collapsed={collapsed} setCollapsed={setCollapsed} userAddress={userAddress}/>
          </div>
          <div className="col-lg-9 px-0 px-lg-3">
            <Routes>
              <Route path='/' element={<Dashboard/>} />
              <Route path='/account' element={<Account userAddress={userAddress}/>} />
              <Route path='/calculator' element={<Calculator userAddress={userAddress}/>} />
              <Route path='/swap' element={<Swap userAddress={userAddress}/>}/>
            </Routes>
          </div>
        </div>
      </Router>
    </div>
  );
}

export default App;
