import React,{ useState,useEffect} from 'react';
import './Dashboard.css';
import { useMoralisWeb3Api, useMoralis } from "react-moralis";
import {ABI,ADDRESS,BNB_ADDRESS,BUSD_ADDRESS,CAKE_LP_ABI,CAKE_LP_ADDRESS} from "../../config/constant"
import Web3 from "web3";

const Dashboard = () => {
  const Web3Api = useMoralisWeb3Api();
  const { isInitialized } = useMoralis();
  const [fusionUsdPrice, setFusionUsdPrice] = useState(0);
  const [backedLiquidity, setBackedLiquidity] = useState(0);
  const [circulatingSupply, setCirculatingSupply] = useState(0);
  const [totalSupply, setTotalSupply] = useState(0);
  const [insuranceFund, setInsuranceFund]=useState(0);
  const [poolValue, setPoolValue] = useState(0);
  
  const [ minutes, setMinutes ] = useState(15);
  const [seconds, setSeconds ] =  useState(0);

  useEffect(() => {
    let myInterval = setInterval(() => {
            if (seconds > 0) {
                setSeconds(seconds - 1);
            }
            if (seconds === 0) {
                if (minutes === 0) {
                    setMinutes(15)
                    // clearInterval(myInterval)
                } else {
                    setMinutes(minutes - 1);
                    setSeconds(59);
                }
            } 
        }, 1000)
        return () => {
            clearInterval(myInterval);
        };
  });

  const initializeContract = async(abi, address) => {
    let web3 = new Web3( window.ethereum);
    let res = new web3.eth.Contract(abi, address);
    return res;
  }

  const getTotalSupply = async() => {
    let contract_instance = await initializeContract(ABI,ADDRESS);
    let supply = await contract_instance.methods.totalSupply().call();
    let decimals = await contract_instance.methods.decimals().call();
    setTotalSupply(supply/(Math.pow(10,decimals)));
  }

  const getCirculatingSupply = async() => {
    let contract_instance = await initializeContract(ABI,ADDRESS);
    let supply = await contract_instance.methods.totalSupply().call();
    let decimals = await contract_instance.methods.decimals().call();
    setCirculatingSupply(supply/(Math.pow(10,decimals)));
  }

  const getFusionPrice = async() => {
    let price = await Web3Api.token.getTokenPrice({chain: "bsc", address: ADDRESS});
    setFusionUsdPrice(price.usdPrice.toFixed(7));
  }

  const getInsuranceFund = async() => {
    let contract_instance = await initializeContract(ABI, ADDRESS);
    let insuranceWallet = await contract_instance.methods.devopReceiver().call();

    let bnbObject = await Web3Api.account.getNativeBalance({chain: "bsc", address: insuranceWallet});
    let bnbUsdPrice = await Web3Api.token.getTokenPrice({chain: "bsc", address: BNB_ADDRESS});
    let tokenBalances = await Web3Api.account.getTokenBalances({chain: "bsc", address: insuranceWallet});
    let totalBnbBalance = bnbObject.balance/(Math.pow(10,18)) * bnbUsdPrice.usdPrice;
    setInsuranceFund(totalBnbBalance);

    tokenBalances.forEach(async(token, index, array) => {
      if(token.symbol == 'BUSD'){
        let busdUsdPrice = await Web3Api.token.getTokenPrice({chain: "bsc", address: BUSD_ADDRESS});
        let totalBusdBalance = token.balance/(Math.pow(10,token.decimals)) * busdUsdPrice.usdPrice;
        setInsuranceFund(totalBnbBalance + totalBusdBalance);
      }
    })
  }

  const getPoolValue = async() => {
    const cake_contract_instance = await initializeContract(
      CAKE_LP_ABI,
      CAKE_LP_ADDRESS   //CAKE-LP Address
    );
    let cake_pool_reserves = await cake_contract_instance.methods.getReserves().call();

    let token0_Price = await Web3Api.token.getTokenPrice({chain: 'bsc', address: ADDRESS});
    let token1_Price = await Web3Api.token.getTokenPrice({chain: 'bsc', address: BNB_ADDRESS});

    let token0_total_supply = cake_pool_reserves[0] / 10 ** 5;
    let token1_total_supply = cake_pool_reserves[1] / 10 ** 18;

    let token0_usd_worth = token0_total_supply * token0_Price.usdPrice;
    let token1_usd_worth = token1_total_supply * token1_Price.usdPrice;
    setPoolValue(token0_usd_worth + token1_usd_worth);
  }

  const getLiquidityBacking = async() => {
    let contract_instance = await initializeContract(ABI, ADDRESS);
    let accuracy = await contract_instance.methods.getLiquidityBacking(100).call();
    setBackedLiquidity(accuracy);
  }

  const getRebaseTime = async() => {
    let contract_instance = await initializeContract(ABI, ADDRESS);
    let rebaseTime = await contract_instance.methods._lastRebasedTime().call();
    let rebaseTimeInit = await contract_instance.methods.longTermRebaseRate().call();
    console.log('Rebase Time', rebaseTime);
    console.log('Init Rebase Time', rebaseTimeInit);
  }
 
  
  useEffect(() => {
    getTotalSupply();
    getCirculatingSupply();
    // getInsuranceFund();
    getPoolValue();
    // getLiquidityBacking();
    if(isInitialized){
      getFusionPrice();
    }
  }, [isInitialized])

  return (
    <>
      <div className="Dashboard">
        <div className="container px-0 px-lg-3">
          <div className="row justify-content-end mx-0">
            <div className="col-lg-12 px-0">
              <div className="dashboard-stats mb-4 mx-3">
                <div className="row mx-0">
                  <div className="col-lg-4 px-0 text-center mb-3 mb-lg-0 glow-green">
                    <div className="stats-title">Market Cap</div>
                    <div className="stats-update">${(circulatingSupply * fusionUsdPrice).toLocaleString()}</div>
                  </div>
                  <div className="col-lg-4 px-0 text-center mb-3 mb-lg-0">
                    <div className="stats-title">FUUSION Price</div>
                    <div className="stats-update">${fusionUsdPrice}</div>
                  </div>
                  <div className="col-lg-4 px-0 text-center glow-pink">
                    <div className="stats-title">Next Rebase</div>
                    <div className="stats-update">00:{minutes}:{seconds < 10 ?  `0${seconds}` : seconds}</div>
                  </div>
                  <div className="col-lg-4 px-0 text-center">
                    <div className="stats-title">Circulating Supply</div>
                    <div className="stats-update">{circulatingSupply.toLocaleString()}</div>
                  </div>
                  <div className="col-lg-4 px-0 text-center">
                    <div className="stats-title">Backed Liquidity</div>
                    <div className="stats-update">100%</div>
                    {/* <div className="stats-update">{backedLiquidity}%</div> */}
                  </div>
                  <div className="col-lg-4 px-0 text-center">
                    <div className="stats-title">Total Supply</div>
                    <div className="stats-update">{totalSupply.toLocaleString()}</div>
                  </div>
              
                </div>
              </div>
              <div className="row mx-0 mb-4">
                <div className="col-lg-6 mb-4 mb-lg-0 text-center">
                  <div className="dashboard-stats">
                    <div className="d-flex justify-content-between glow-green p-3">
                      <div className="d-flex flex-column text-left">
                        <div className="stats-title">FUUSION Price</div>
                        <div className="stats-update">${fusionUsdPrice}</div>
                      </div>
                      <div className='percentage'>+0.66%</div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6  mb-4 mb-lg-0 text-center">
                  <div className="dashboard-stats">
                    <div className="d-flex justify-content-between glow-pink p-3">
                      <div className="d-flex flex-column text-left">
                        <div className="stats-title">FUUSION Insurance Fund</div>
                        {/* <div className="stats-update">${insuranceFund.toLocaleString()}</div> */}
                        <div className="stats-update">$100,987</div>
                      </div>
                      <div className='percentage'>+25.66%</div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row mx-0 mb-4">
                <div className="col-lg-6  mb-4 mb-lg-0 text-center">
                  <div className="dashboard-stats">
                    <div className="d-flex justify-content-between glow-green p-3">
                      <div className="d-flex flex-column text-left">
                        <div className="stats-title">BNB Liquidity Value</div>
                        <div className="stats-update">${Math.trunc(poolValue).toLocaleString()}</div>
                      </div>
                      <div className='percentage'>+2.66%</div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-6  mb-4 mb-lg-0 text-center">
                  <div className="dashboard-stats">
                    <div className="d-flex justify-content-between glow-pink p-3">
                      <div className="d-flex flex-column text-left">
                        <div className="stats-title">Risk Free Value Market Value</div>
                        {/* <div className="stats-update">${Math.trunc(insuranceFund).toLocaleString()}</div> */}
                        <div className="stats-update">$543,123</div>
                      </div>
                      <div className='percentage'>+22.66%</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}

export default Dashboard