import { useState, useEffect} from 'react';
import { useMoralisWeb3Api, useMoralis } from "react-moralis";
import {ADDRESS} from "../../config/constant"
import './Calculator.css';
import InputRange from "react-input-range";
import 'react-input-range/lib/css/index.css';

const Calculator = ({userAddress}) => {
  const Web3Api = useMoralisWeb3Api();
  const { isInitialized } = useMoralis();
  const [fusionUsdPrice, setFusionUsdPrice] = useState(0);
  const [userFusionBalance, setUserFusionBalance] = useState(0);
  const [fusionAmount, setFusionAmount] = useState(0);
  const [range, setRange] = useState(30);
  const [fusionTempPrice, setFusionTempPrice] = useState(0);
  const [fusionFuturePrice, setFusionFuturePrice] = useState(0);

  const getFusionPrice = async() => {
    let price = await Web3Api.token.getTokenPrice({chain: "bsc", address: ADDRESS});
    setFusionUsdPrice(price.usdPrice.toFixed(7));
    setFusionTempPrice(price.usdPrice.toFixed(7));
    setFusionFuturePrice(price.usdPrice.toFixed(7));
  }

  const getUserBalance = async() => {
    let tokenBalances = await Web3Api.account.getTokenBalances({chain: "bsc", address: userAddress});

    tokenBalances.forEach((token, index, array) => {
      // if(token.symbol == "CA" && token.name == "Contract"){
      //   setUserFusionBalance(token.balance/(Math.pow(10,token.decimals)));
      //   setFusionAmount(token.balance/(Math.pow(10,token.decimals)));
      // }
      if(token.token_address == ADDRESS.toLowerCase()){
        setUserFusionBalance(token.balance/(Math.pow(10,token.decimals)));
        setFusionAmount(token.balance/(Math.pow(10,token.decimals)));
      }
    })
  }

  const onCurrentPriceChange = (event) => {
    setFusionTempPrice(event.target.value)
  }

  const onFuturePriceChange = (event) => {
    setFusionFuturePrice(event.target.value)
  }

  const onFusionAmountChange = (event) => {
    setFusionAmount(event.target.value)
  }

  const onFusionMaxClick = () => {
    if(userAddress){
      setFusionAmount(userFusionBalance)
    }
  }

  const onCurrentPriceClick = () => {
    setFusionTempPrice(fusionUsdPrice)  
  }

  const onFuturePriceClick = () => {
    setFusionFuturePrice(fusionUsdPrice)  
  }

  const calculateRewards = (days, amount) => {
    let rewardCount = (days * 24 * 60) / 15;
    let investment = Number(amount);
    for(let i = 0;i < rewardCount;i++) {
      investment += Number(investment * 0.0002084);
    }
    return investment;
  }

  useEffect(() => {
    if(isInitialized){
      getUserBalance();
      getFusionPrice();
    }
  }, [isInitialized])

  return (
    <>
      <div className="Calculator">
        <div className="container">
          <div className="row justify-content-end mx-0">
            <div className="col-lg-12 px-0 px-lg-3">
              <div className="calculator-view">
                <div className="calculator-card mb-4">
                  <div className="fs-20 fw-700 textGray mb-1">Calculator</div>
                  <div className="fs-12 fw-600 textGray mb-1">Estimate your returns</div>
                  <div className="row mx-0 my-4">
                    <div className="col-lg-4 px-0 text-lg-center">
                      <div className="glow-green p-3">
                      <div className="metric-title">FUUSION Price</div>
                      <div className="metric-value">${fusionUsdPrice}</div>
                      </div>
                    </div>
                    <div className="col-lg-4 col-6 px-0 text-lg-center">
                      <div className="metric-title">Current APY</div>
                      <div className="metric-value">100,000%</div>
                    </div>
                    <div className="col-lg-4 col-6 px-0 text-lg-center">
                      <div className="glow-pink p-3">
                      <div className="metric-title">Your FUUSION  Balance</div>
                      <div className="metric-value">{userFusionBalance.toFixed(2).toLocaleString()} FUU</div>
                      </div>
                    </div>
                  </div>
                  <div className="row mx-0 mb-4">
                    <div className="col-lg-6 px-0 mb-4">
                      <label>FUUSION Amount</label>
                      <div className="input-with-value">
                        <input type="number" placeholder='0' value={fusionAmount} onChange={onFusionAmountChange}/>
                        <div className="second-value pointer" onClick={() => onFusionMaxClick()}>Max</div>
                      </div>
                    </div>
                    <div className="col-lg-6 px-0 px-lg-3 mb-4">
                      <label>APY (%)</label>
                      <div className="input-with-value">
                        <input type="text" placeholder='383025.8' value={100000} disabled={true}/>
                        <div className="second-value pointer">Current</div>
                      </div>
                    </div>
                    <div className="col-lg-6 px-0 mb-4">
                      <label>FUUSION price at purchase ($)</label>
                      <div className="input-with-value">
                        <input type="number" placeholder='1.27' value={fusionTempPrice} onChange={onCurrentPriceChange}/>
                        <div className="second-value pointer" onClick={() => onCurrentPriceClick()}>Current</div>
                      </div>
                    </div>
                    <div className="col-lg-6 px-0 px-lg-3 mb-4">
                      <label> Future FUUSION market price ($)</label>
                      <div className="input-with-value">
                        <input type="number" placeholder='1.27' value={fusionFuturePrice} onChange={onFuturePriceChange}/>
                        <div className="second-value pointer" onClick={() => onFuturePriceClick()}>Current</div>
                      </div>
                    </div>
                  </div>
                  <div className="fs-16 fw-500 textGray mb-4 px-3">{range} days</div>
                  <div className="inputRange px-3 mb-5">
                    <InputRange maxValue={365} minValue={1} value={range} onChange={(value) => { setRange(value); }} />
                  </div>
                  <div className="px-3">
                    <div className="d-flex justify-content-between align-items-center">
                      <div className="fs-14 fw-500 textGray">Your initial investment</div>
                      <div className="fs-14 textGray">${(fusionTempPrice * fusionAmount).toFixed(4)}</div>
                    </div>
                    <div className="d-flex justify-content-between align-items-center my-3">
                      <div className="fs-14 fw-500 textGray">Current wealth</div>
                      <div className="fs-14 textGray">${(fusionTempPrice * fusionAmount).toFixed(4)}</div>
                    </div>
                    <div className="d-flex justify-content-between align-items-center mb-3">
                      <div className="fs-14 fw-500 textGray">FUUSION rewards estimation</div>
                      <div className="fs-14 textGray">{(calculateRewards(range, fusionAmount)).toFixed(4)} FUUSION</div>
                    </div>
                    <div className="d-flex justify-content-between align-items-center mb-3">
                      <div className="fs-14 fw-500 textGray">Potential return</div>
                      <div className="fs-14 textGray">${(calculateRewards(range, fusionAmount)).toFixed(4) * fusionFuturePrice}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}

export default Calculator