import React from 'react';
import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";
import './Header.css';
import logo from '../../../assets/logo.png'
import {useState, useEffect} from 'react';
import Web3 from 'web3';
import {ADDRESS} from "../../../config/constant";
let web3 = new Web3((window).ethereum);

const Header = ({collapsed, setCollapsed, setUserWalletAddress}) => {
  const [userAddress, setUserAddress] = useState('');

  const handleToggleClick = () => {
    setCollapsed(!collapsed);
  };

  const connectWallet = async() => {
    if (!(window).ethereum) {
      window.alert('Please install MetaMask first.');
      return;
    }

    if (!web3) {
        try {
          // Request account access if needed
          await (window).ethereum.enable();
          // We don't know window.web3 version, so we use our own instance of Web3
          // with the injected provider given by MetaMask
          web3 = new Web3((window).ethereum);
        } catch (error) {
          window.alert('You need to allow MetaMask.');
          return;
        }
    }

    await (window).ethereum.enable();
    let publicAddress = await web3.eth.getCoinbase();

    try {
      await web3.eth.personal.sign(
        `Connecting Wallet To Fusion!`,
        publicAddress,
        '' // MetaMask will ignore the password argument here
      );
      localStorage.setItem('userAddress', publicAddress);
      setUserAddress(publicAddress);
      setUserWalletAddress(publicAddress);
    } catch (err) {
      throw new Error(
        'You need to sign the message to be able to Sign Up.'
      );
    }
  }

  const connectMobileWallet = async () => {
    console.log('Connect Mobile');
    const connector = new WalletConnect({
      bridge: "https://bridge.walletconnect.org", // Required
      qrcodeModal: QRCodeModal,
    });
    // Check if connection is already established
    if (!connector.connected) {
      // create new session
      connector.createSession();
    }

    // Subscribe to connection events
    connector.on("connect", (error, payload) => {
      if (error) {
        throw error;
      }

      // Get provided accounts and chainId
      const { accounts, chainId } = payload.params[0];
    });

    connector.on("session_update", (error, payload) => {
      if (error) {
        throw error;
      }
      // Get updated accounts and chainId
      const { accounts, chainId } = payload.params[0];
    });
  }

  const disconnectMobileWallet = async () => {

  }

  const disconnectWallet = () => {
    localStorage.removeItem('userAddress');
    setUserWalletAddress('');
    setUserAddress('');
  }

  useEffect(() => {
    setUserAddress(localStorage.getItem('userAddress'));
    setUserWalletAddress(localStorage.getItem('userAddress'));
  },[])
  
  return (
    <>
      <div className="dashboardNav">
        <nav class="navbar navbar-expand-sm mt-4">
          <div className="container-fluid px-0 px-lg-3">
            <a href="https://www.fuusion.finance/" className="navbar-brand d-none d-lg-block"><img src={logo} alt="" /></a>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
              <ul class="navbar-nav ml-auto position-relative px-3 d-none d-lg-flex">
                <li class="nav-item mr-3 active">
                  <div class="dropdown">
                    <a class="btn connectWallet dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-expanded="false">
                    $FUUSION
                    </a>
                    {/* $0.0632 USD */}
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                      <a class="dropdown-item" target="_blank" rel="noopener noreferrer" href={"https://pancakeswap.finance/swap?outputCurrency="+ADDRESS}>Buy on Pancakeswap</a>
                      <a class="dropdown-item" target="_blank" rel="noopener noreferrer" href={"https://charts.bogged.finance/?c=bsc&t="+ADDRESS}>Chart</a>
                    </div>
                  </div>
                </li>
                <li class="nav-item">
                  {userAddress && <a class="btn connectWallet" onClick={() => disconnectWallet()}>Disconnect</a>}
                  {!userAddress && <a class="btn connectWallet" onClick={() => connectWallet()}>Connect Wallet</a>}
                </li>
              </ul>
            </div>
            <ul className="list-usntyled d-flex justify-content-between align-items-center d-lg-none w-100">
              <li><a className='textGray toggle-btn' onClick={() => handleToggleClick()}><i className="fas fa-bars"></i></a></li>
              <li class="nav-item">
                <a class="btn connectWallet">FUUSION </a>
                {/* <a class="btn connectWallet">FUUSION <small>$0.0672</small></a> */}
              </li>
              <li class="nav-item">
                {!userAddress && <a class="btn connectWallet" onClick={() => disconnectWallet()}>Disconnect</a>}
                {userAddress && <a class="btn connectWallet" onClick={() => connectWallet()}>Connect Wallet</a>}
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </>
  )
};

export default Header;